.el-btn {
  display: inline-block;
  width: 300px;
  max-width: 100%;
  padding: 20px 10px;
  text-align: center;
  color: #181818;
  margin-bottom: 16px;
  border: 1px solid currentColor;
  transition: .25s; }
  .el-btn:hover, .el-btn:focus {
    background: #181818;
    color: #fff; }
  .el-btn--center {
    display: block;
    margin-left: auto;
    margin-right: auto; }
  .el-btn--angle {
    position: relative; }
    .el-btn--angle:after {
      right: 20px;
      width: 8px;
      height: 8px;
      border-style: solid;
      border-width: 1px 1px 0 0;
      border-color: #181818;
      transform: translate(0, -50%) rotate(45deg);
      content: "";
      display: block;
      position: absolute;
      top: 50%;
      transition: border-color cubic-bezier(0.215, 0.61, 0.355, 1) 0.15s, -webkit-transform cubic-bezier(0.215, 0.61, 0.355, 1) 0.15s;
      transition: transform cubic-bezier(0.215, 0.61, 0.355, 1) 0.15s, border-color cubic-bezier(0.215, 0.61, 0.355, 1) 0.15s;
      transition: transform cubic-bezier(0.215, 0.61, 0.355, 1) 0.15s, border-color cubic-bezier(0.215, 0.61, 0.355, 1) 0.15s, -webkit-transform cubic-bezier(0.215, 0.61, 0.355, 1) 0.15s;
      content: ""; }
    .el-btn--angle:hover::after, .el-btn--angle:focus::after {
